<template>
  <view class="u-upload">
    <slot name="upload-header">
      <view class="cu-bar bg-white margin-top">
        <view class="action">图片上传</view>
        <view class="action">{{'' + imgList.length + '/' + imgLength }}</view>
      </view>
    </slot>
    <view class="cu-form-group">
      <view class="grid col-4 grid-square flex-sub">
        <block v-for="(item, index) in imgList" :key="index">
          <view class="bg-img">
            <image :src="imgList[index]" mode="aspectFill"></image>
            <view class="cu-tag bg-red" @click="DelImg">
              <text class="cuIcon-close"></text>
            </view>
          </view>
        </block>
        <block v-if="imgList.length < imgLength">
          <view
            class="solids"
            @click="chooseImage"
          >
            <text class="cuIcon-cameraadd"></text>
          </view>
        </block>
      </view>
    </view>
  </view>

</template>

<script>
export default {
  name: 'UUpload',
  props: {
    imgLength: {
      type: Number | String,
      default: 9,
    },
  },
  computed: {},
  data() {
    return {
      imgList: []
    }
  },
  beforeCreate() {
    // 在实例初始化之后被调用
    console.log('component beforeCreate');
  },
  created() {
    // 在实例创建完成后被立即调用
    console.log('component created');
  },
  beforeMount() {
    // 在挂载开始之前被调用
    console.log('component beforeMount');
  },
  mounted() {
    // 挂载到实例上去之后调用
    // 注意：此处并不能确定子组件被全部挂载，如果需要子组件完全挂载之后在执行操作可以使用$nextTick
    console.log('component mounted');
  },
  beforeUpdate() {
    // 数据更新时调用，发生在虚拟 DOM 打补丁之前【仅H5平台支持】
    console.log('component beforeUpdate');
  },
  updated() {
    // 由于数据更改导致的虚拟 DOM 重新渲染和打补丁，在这之后会调用该钩子【仅H5平台支持】
    console.log('component updated');
  },
  beforeDestroy() {
    // 实例销毁之前调用。在这一步，实例仍然完全可用
    console.log('component beforeDestroy');
  },
  destroyed() {
    // Vue 实例销毁后调用。调用后，Vue 实例指示的所有东西都会解绑定，所有的事件监听器会被移除，所有的子实例也会被销毁
    console.log('component destroyed');
  },
  methods: {
    chooseImage() {
      // var _this = this;
      uni.chooseImage({
        count: this.imgLength, // 默认9
        sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
        sourceType: ['album', 'camera'], // album 从相册选图，camera 使用相机，默认二者都有。
        success: res => {
          if (this.imgList.length != 0) {
            this.imgList = this.imgList.concat(res.tempFilePaths)
          } else {
            this.imgList = res.tempFilePaths
          }
        },
        fail: err => {
          console.log(err)
        },
        complete: complete => {
          console.log(complete)
        }
      })
    },
    DelImg(e) {
      uni.showModal({
        title: '召唤师',
        content: '确定要删除这段回忆吗？',
        cancelText: '再看看',
        confirmText: '再见',
        success: (res) => {
          if (res.confirm) {
            this.imgList.splice(e.currentTarget.dataset.index, 1);
          }
        }
      })
    },
  },
};
</script>

<style></style>
